import { Cancel } from '@material-ui/icons'
import * as React from 'react'
import {
  Create,
  SimpleForm,
  TextInput,
  CreateProps,
  useTranslate,
  required,
  Toolbar,
  ToolbarProps,
  SaveButton,
  ListButton,
} from 'react-admin'

const CreateToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ListButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const ObjectCreate = (props: CreateProps) => {
  const translate = useTranslate()
  return (
    <Create {...props} title={translate('resources.objects.create')}>
      <SimpleForm redirect="list" toolbar={<CreateToolbar />}>
        <TextInput source="name" validate={required()} />
      </SimpleForm>
    </Create>
  )
}

export default ObjectCreate
