import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  Edit,
  EditProps,
  ReferenceInput,
  SaveButton,
  ShowButton,
  SimpleForm,
  TextField,
  TextInput,
  TitleProps,
  Toolbar,
  ToolbarProps,
  useEditContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../core/auth/Authority'
import { DeviceCategoryDto } from '../../core/dto/device-category/device-category.dto'
import { ResourceName } from '../../core/ResourceName'
import { SelectWithPaginationInput } from '../common/SelectWithPaginationInput'
import { useHasAuthority } from '../hooks/useHasAuthority'

const validateDeviceCategoryEdit = (values: DeviceCategoryDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  return errors
}

interface DeviceCategoryTitleProps extends TitleProps {
  record?: DeviceCategoryDto
}

const DeviceCategoryTitle = (data: DeviceCategoryTitleProps) => {
  const { record } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.device-categories.titles.edit')}: {record?.id}.{' '}
      {record?.name}{' '}
    </span>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ShowButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const DeviceCategory = ({ ...props }) => {
  const { record: deviceCategory } = useEditContext<DeviceCategoryDto>()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <SimpleForm
      {...props}
      redirect="show"
      toolbar={<EditToolbar />}
      validate={validateDeviceCategoryEdit}
    >
      <TextField source="id" label="ID" />
      <TextInput source="name" />
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceInput
          source="parentId"
          reference={ResourceName.DEVICE_CATEGORIES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
          filter={{ notId: deviceCategory?.id }}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </SimpleForm>
  )
}

export const DeviceCategoryEdit = (props: EditProps) => (
  <Edit {...props} hasShow={false} title={<DeviceCategoryTitle />}>
    <DeviceCategory />
  </Edit>
)
