const mapping: Record<
  string,
  (
    lambdaParameter: string,
    value: string | number | boolean,
    otherValues: any,
  ) => string | null
> = {
  name: (lambdaParameter, value) =>
    `${lambdaParameter}.Name.Contains("${value}")`,
  filterAccessId: (lambdaParameter, value, otherValues) =>
    value
      ? `${lambdaParameter}.Accesses.Any(a=>a.Id==${otherValues?.accessId})`
      : null,
  accessId: () => null,
}

const filterMapper = (key: string) =>
  mapping[key] as (
    lambdaParameter: string,
    value: string | number | boolean,
    otherValues: any,
  ) => string

export default filterMapper
