import { Box, Divider } from '@material-ui/core'
import React, { useMemo } from 'react'
import {
  BooleanInput,
  DateField,
  EditButton,
  Filter,
  FunctionField,
  Record,
  sanitizeFetchType,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  TextInput,
  TitleProps,
  useMutation,
  useRefresh,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../core/auth/Authority'
import { EventDto } from '../../core/dto/event/event.dto'
import { EventStatus } from '../../core/enum/EventStatus'
import { ResourceName } from '../../core/ResourceName'
import Button from '../common/customized-mui-components/Button'
import FilterProps from '../common/FilterProps'
import RelationTab from '../common/RelationTab'
import { useHasAuthority } from '../hooks/useHasAuthority'

interface EventTitleProps extends TitleProps {
  record?: EventDto
}

const ShowTitle = (data: EventTitleProps) => {
  const { record: event } = data
  const translate = useTranslate()
  const date = useMemo(
    () =>
      !((event?.dateStart as any) instanceof Date)
        ? event?.dateStart.replace('T', ' ')
        : '',
    [event?.dateStart],
  )
  return (
    <span>
      {translate('resources.events.titles.show')}: {event?.name} {date}{' '}
      {event?.id}
    </span>
  )
}

const GeneralActions = () => {
  const { record: event } = useShowContext<EventDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()

  const handleActivation = async () => {
    if (event?.status !== EventStatus.IN_TRANSITION) {
      if (event?.status === EventStatus.ACTIVE) {
        await mutate(
          {
            type: sanitizeFetchType('deactivate'),
            resource: ResourceName.EVENTS,
            payload: {
              eventId: event?.id,
            },
          },
          { returnPromise: false },
        )
      } else {
        await mutate(
          {
            type: sanitizeFetchType('activate'),
            resource: ResourceName.EVENTS,
            payload: {
              eventId: event?.id,
            },
          },
          { returnPromise: false },
        )
      }
      refresh()
    }
  }

  return (
    <>
      <Divider />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button
          variant="contained"
          onClick={handleActivation}
          color="primary"
          style={{ marginRight: '5px' }}
          label={
            event?.status === EventStatus.INACTIVE
              ? translate('resources.events.actions.activate')
              : translate('resources.events.actions.deactivate')
          }
          useSmallVersionBreakpoint={false}
          disabled={
            !hasAuthority(Authority.EDIT_EVENTS) ||
            event?.status === EventStatus.IN_TRANSITION
          }
        />
        <EditButton
          variant="contained"
          basePath="/events"
          record={event}
          disabled={!hasAuthority(Authority.EDIT_EVENTS)}
        />
      </Box>
    </>
  )
}

const EventTabs = ({ ...props }) => {
  const translate = useTranslate()
  const { record: event } = props

  const CompaniesFilters = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterEventId"
        alwaysOn
      />
    </Filter>
  )

  const ObjectsFilters = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterEventId"
        alwaysOn
      />
    </Filter>
  )

  const EnginesFilters = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterEventId"
        alwaysOn
      />
      <TextInput source="categoryId" />
    </Filter>
  )

  const PalmsFilters = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterEventId"
        alwaysOn
      />
      <TextInput source="categoryId" />
    </Filter>
  )

  return (
    <TabbedShowLayout {...props}>
      <Tab label={translate('resources.events.tabs.general')}>
        <TextField source="id" label="ID" />
        <TextField source="name" />
        <DateField source="dateStart" showTime />
        <DateField source="dateEnd" showTime />
        <FunctionField
          source="status"
          render={(r: Record | undefined) => (
            <TextField
              emptyText={translate(
                `resources.enums.eventStatus.${r?.status.toLowerCase()}`,
              )}
            />
          )}
        />
        <GeneralActions />
      </Tab>
      <Tab
        path="companies"
        label={translate('resources.events.tabs.companies')}
      >
        <>
          <RelationTab<EventDto>
            resource={ResourceName.COMPANIES}
            source="companiesIds"
            mode="edit"
            attachMethod="attachCompanies"
            detachMethod="detachCompanies"
            attachRequestPayload={(r, ids) => ({
              eventId: r.id,
              companiesIds: ids,
            })}
            detachRequestPayload={(r, ids) => ({
              eventId: r.id,
              companiesIds: ids,
            })}
            refetchListAfterChange={(filters) =>
              filters?.filterEventId === true
            }
            relationsRequestMethod="getCompanies"
            relationsRequestPayload={(r) => ({ eventId: r.id })}
            filters={<CompaniesFilters />}
            filterDefaultValues={{
              eventId: event?.id,
              filterEventId: true,
            }}
            sort={{
              field: 'id',
              order: 'DESC',
            }}
          >
            <TextField source="id" label="ID" />
            <TextField source="name" key="name" />
          </RelationTab>
        </>
      </Tab>
      <Tab path="objects" label={translate('resources.events.tabs.objects')}>
        <>
          <RelationTab<EventDto>
            resource={ResourceName.OBJECTS}
            source="objectsIds"
            mode="edit"
            attachMethod="attachObjects"
            detachMethod="detachObjects"
            attachRequestPayload={(r, ids) => ({
              eventId: r.id,
              objectsIds: ids,
            })}
            detachRequestPayload={(r, ids) => ({
              eventId: r.id,
              objectsIds: ids,
            })}
            refetchListAfterChange={(filters) =>
              filters?.filterEventId === true
            }
            filters={<ObjectsFilters />}
            filterDefaultValues={{
              eventId: event?.id,
              filterEventId: true,
            }}
            sort={{
              field: 'id',
              order: 'DESC',
            }}
          >
            <TextField source="id" label="ID" />
            <TextField source="name" key="name" />
          </RelationTab>
        </>
      </Tab>
      <Tab path="engines" label={translate('resources.events.tabs.engines')}>
        <>
          <RelationTab<EventDto>
            resource={ResourceName.ENGINES}
            source="verifierDevicesIds"
            mode="edit"
            attachMethod="attachDevices"
            detachMethod="detachDevices"
            attachRequestPayload={(r, ids) => ({
              eventId: r.id,
              verifierDevicesIds: ids,
            })}
            detachRequestPayload={(r, ids) => ({
              eventId: r.id,
              verifierDevicesIds: ids,
            })}
            refetchListAfterChange={(filters) =>
              filters?.filterEventId === true
            }
            relationsRequestMethod="getDevices"
            relationsRequestPayload={(r) => ({ eventId: r.id })}
            filters={<EnginesFilters />}
            filterDefaultValues={{
              eventId: event?.id,
              filterEventId: true,
            }}
            sort={{
              field: 'id',
              order: 'DESC',
            }}
          >
            <TextField source="id" label="ID" />
            <TextField source="name" key="name" />
          </RelationTab>
        </>
      </Tab>
      <Tab path="palms" label={translate('resources.events.tabs.palms')}>
        <>
          <RelationTab<EventDto>
            resource={ResourceName.PALMS}
            source="verifierDevicesIds"
            mode="edit"
            attachMethod="attachDevices"
            detachMethod="detachDevices"
            attachRequestPayload={(r, ids) => ({
              eventId: r.id,
              verifierDevicesIds: ids,
            })}
            detachRequestPayload={(r, ids) => ({
              eventId: r.id,
              verifierDevicesIds: ids,
            })}
            refetchListAfterChange={(filters) =>
              filters?.filterEventId === true
            }
            relationsRequestMethod="getDevices"
            relationsRequestPayload={(r) => ({ eventId: r.id })}
            filters={<PalmsFilters />}
            filterDefaultValues={{
              eventId: event?.id,
              filterEventId: true,
            }}
            sort={{
              field: 'id',
              order: 'DESC',
            }}
          >
            <TextField source="id" label="ID" />
            <TextField source="name" key="name" />
          </RelationTab>
        </>
      </Tab>
    </TabbedShowLayout>
  )
}

export const EventShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <EventTabs />
  </Show>
)
