import { Box, Divider } from '@material-ui/core'
import React, { useState } from 'react'
import {
  BooleanInput,
  DateField,
  EditButton,
  Filter,
  FunctionField,
  ReferenceField,
  sanitizeFetchType,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  TextInput,
  TitleProps,
  useMutation,
  useRefresh,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { PalmDto } from '../../../core/dto/device/palm/palm.dto'
import { ResourceName } from '../../../core/ResourceName'
import Button from '../../common/customized-mui-components/Button'
import FilterProps from '../../common/FilterProps'
import RelationTab from '../../common/RelationTab'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { ConfirmDialog, DeviceIdModal, LockModal } from './PalmDialogs'

interface PalmTitleProps extends TitleProps {
  record?: PalmDto
}

const ShowTitle = (data: PalmTitleProps) => {
  const { record: palm } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.palms.titles.show')}: {palm?.id}. {palm?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: palm } = useShowContext<PalmDto>()
  const [deviceIdModalOpen, setDeviceIdModalOpen] = useState(false)
  const [lockModalOpen, setLockModalOpen] = useState(false)
  const [unlockDialogOpen, setUnlockDialogOpen] = useState(false)
  const [resetIdDialogOpen, setResetIdDialogOpen] = useState(false)
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const hasAuthority = useHasAuthority()

  const handleSetMobileDeviceId = async () => {
    setResetIdDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('resetMobileDeviceId'),
        resource: ResourceName.PALMS,
        payload: {
          palmId: palm?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleSetMobileDeviceIdDialog = () => {
    if (palm?.mobileDeviceId) setResetIdDialogOpen(true)
    else setDeviceIdModalOpen(true)
  }

  const handleLock = async () => {
    setUnlockDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('unlock'),
        resource: ResourceName.PALMS,
        payload: {
          palmId: palm?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleLockDialog = () => {
    if (palm?.disabled) setUnlockDialogOpen(true)
    else setLockModalOpen(true)
  }

  return (
    <>
      <Divider />
      <DeviceIdModal
        open={deviceIdModalOpen}
        close={() => setDeviceIdModalOpen(false)}
      />
      <LockModal open={lockModalOpen} close={() => setLockModalOpen(false)} />
      <ConfirmDialog
        open={unlockDialogOpen}
        confirm={handleLock}
        source="lock"
        cancel={() => setUnlockDialogOpen(false)}
        action="remove"
      />
      <ConfirmDialog
        open={resetIdDialogOpen}
        confirm={handleSetMobileDeviceId}
        source="deviceId"
        cancel={() => setResetIdDialogOpen(false)}
        action="remove"
      />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button
          variant="contained"
          onClick={handleSetMobileDeviceIdDialog}
          color="primary"
          style={{ margin: '2px' }}
          label={
            palm?.mobileDeviceId
              ? 'resources.palms.actions.resetMobileDeviceId'
              : 'resources.palms.actions.setMobileDeviceId'
          }
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_PALMS)}
        />
        <Button
          variant="contained"
          onClick={handleLockDialog}
          color="primary"
          style={{ margin: '2px' }}
          label={
            palm?.disabled
              ? 'resources.palms.actions.unlock'
              : 'resources.palms.actions.lock'
          }
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_PALMS)}
        />
        <EditButton
          basePath={`/${ResourceName.PALMS}`}
          record={palm}
          variant="contained"
          style={{ margin: '2px' }}
          disabled={!hasAuthority(Authority.EDIT_PALMS)}
        />
      </Box>
    </>
  )
}

const PalmTabs = ({ ...props }) => {
  const translate = useTranslate()
  const { record: palm } = useShowContext<PalmDto>()
  const hasAuthority = useHasAuthority()

  const EventsFilter = (filterProps?: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterVerifierDeviceId"
        alwaysOn
      />
    </Filter>
  )

  const EntrancesFilter = (filterProps?: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="note" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterVerifierDeviceId"
        alwaysOn
      />
    </Filter>
  )

  return (
    <TabbedShowLayout {...props}>
      <Tab label={translate('resources.palms.tabs.general')}>
        <TextField label="ID" source="id" />
        <TextField source="name" />
        <FunctionField<PalmDto>
          source="isWatched"
          render={(record?: PalmDto) => (
            <div>
              {record?.isWatched
                ? translate('const.yes')
                : translate('const.no')}
            </div>
          )}
        />
        <DateField
          source="lastRefresh"
          showTime
          options={{
            day: '2-digit',
            month: '2-digit',
            year: 'numeric',
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit',
          }}
        />
        <FunctionField<PalmDto>
          source="direction"
          render={(record?: PalmDto) => (
            <div>
              {translate(
                `resources.enums.deviceDirection.${record?.direction}`,
              )}
            </div>
          )}
        />
        {palm?.categoryId && hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
          <ReferenceField
            source="categoryId"
            reference={ResourceName.DEVICE_CATEGORIES}
            link="show"
          >
            <TextField source="name" />
          </ReferenceField>
        )}
        {palm?.disabled && <TextField source="disabled" />}
        <FunctionField<PalmDto>
          source="onlineInfixCeck"
          render={(record?: PalmDto) => (
            <div>
              {record?.onlineInfixCeck
                ? translate('const.yes')
                : translate('const.no')}
            </div>
          )}
        />
        <FunctionField<PalmDto>
          source="multiplicity"
          render={(record?: PalmDto) => (
            <div>
              {translate(
                `resources.enums.multiplicities.${record?.multiplicity}`,
              )}
            </div>
          )}
        />
        {palm?.mobileDeviceId && <TextField source="mobileDeviceId" />}
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <Tab path="events" label={translate('resources.engines.tabs.events')}>
          <>
            <RelationTab<PalmDto>
              resource={ResourceName.EVENTS}
              source="eventsIds"
              mode={hasAuthority(Authority.EDIT_EVENTS) ? 'edit' : 'show'}
              attachMethod="attachEvents"
              detachMethod="detachEvents"
              attachRequestPayload={(r, ids) => ({
                palmId: r.id,
                eventsIds: ids,
              })}
              detachRequestPayload={(r, ids) => ({
                palmId: r.id,
                eventsIds: ids,
              })}
              refetchListAfterChange={(filters) =>
                filters?.filterVerifierDeviceId === true
              }
              filters={<EventsFilter />}
              filterDefaultValues={{
                verifierDeviceId: palm?.id,
                filterVerifierDeviceId: true,
              }}
              sort={{
                field: 'id',
                order: 'DESC',
              }}
            >
              <TextField label="ID" source="id" />
              <TextField source="name" />
            </RelationTab>
          </>
        </Tab>
      )}
      {hasAuthority(Authority.VIEW_ENTRANCES) && (
        <Tab
          path="entrances"
          label={translate('resources.engines.tabs.entrances')}
        >
          <>
            <RelationTab<PalmDto>
              resource={ResourceName.ENTRANCES}
              source="entrancesIds"
              mode={hasAuthority(Authority.EDIT_ENTRANCES) ? 'edit' : 'show'}
              attachMethod="attachEntrances"
              detachMethod="detachEntrances"
              attachRequestPayload={(r, ids) => ({
                palmId: r.id,
                entrancesIds: ids,
              })}
              detachRequestPayload={(r, ids) => ({
                palmId: r.id,
                entrancesIds: ids,
              })}
              refetchListAfterChange={(filters) =>
                filters?.filterVerifierDeviceId === true
              }
              filters={<EntrancesFilter />}
              filterDefaultValues={{
                verifierDeviceId: palm?.id,
                filterVerifierDeviceId: true,
              }}
              sort={{
                field: 'id',
                order: 'DESC',
              }}
            >
              <TextField label="ID" source="id" />
              <TextField source="note" />
            </RelationTab>
          </>
        </Tab>
      )}
    </TabbedShowLayout>
  )
}

export const PalmShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <PalmTabs />
  </Show>
)
