import { Box, Divider } from '@material-ui/core'
import React, { useState } from 'react'
import {
  BooleanInput,
  DateField,
  EditButton,
  Filter,
  FunctionField,
  ReferenceField,
  sanitizeFetchType,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  TextInput,
  TitleProps,
  useMutation,
  useRefresh,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { EngineDto } from '../../../core/dto/device/engine/engine.dto'
import { ResourceName } from '../../../core/ResourceName'
import Button from '../../common/customized-mui-components/Button'
import FilterProps from '../../common/FilterProps'
import RelationTab from '../../common/RelationTab'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import {
  ConfirmDialog,
  LockModal,
  SetStandbyModeModal,
  SetTSCanCredentialsModal,
} from './EngineDialogs'

interface EngineTitleProps extends TitleProps {
  record?: EngineDto
}

const ShowTitle = (data: EngineTitleProps) => {
  const { record: engine } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.engines.titles.show')}: {engine?.id}. {engine?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: engine } = useShowContext<EngineDto>()
  const [lockModalOpen, setLockModalOpen] = useState(false)
  const [unlockDialogOpen, setUnlockDialogOpen] = useState(false)
  const [standbyModeModalOpen, setStandbyModeModalOpen] = useState(false)
  const [
    removeTSCanCredentialsDialogOpen,
    setRemoveTSCanCredentialsDialogOpen,
  ] = useState(false)
  const [tsCanCredentialsModalOpen, setTSCanCredentialsModalOpen] =
    useState(false)
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const hasAuthority = useHasAuthority()

  const handleSetTSCanCredentialsDialog = () => {
    setTSCanCredentialsModalOpen(true)
  }

  const handleRemoveTSCanCredentials = async () => {
    setRemoveTSCanCredentialsDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('removeTSCanCredentials'),
        resource: ResourceName.ENGINES,
        payload: {
          engineId: engine?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleRemoveTSCanCredentialsDialog = () => {
    setRemoveTSCanCredentialsDialogOpen(true)
  }

  const handleSetStandbyModeDialog = () => {
    setStandbyModeModalOpen(true)
  }

  const handleLock = async () => {
    setUnlockDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('unlock'),
        resource: ResourceName.ENGINES,
        payload: {
          engineId: engine?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleLockDialog = () => {
    if (engine?.disabled) setUnlockDialogOpen(true)
    else setLockModalOpen(true)
  }

  return (
    <>
      <Divider />
      <SetTSCanCredentialsModal
        open={tsCanCredentialsModalOpen}
        close={() => setTSCanCredentialsModalOpen(false)}
      />
      <SetStandbyModeModal
        open={standbyModeModalOpen}
        close={() => setStandbyModeModalOpen(false)}
      />
      <LockModal open={lockModalOpen} close={() => setLockModalOpen(false)} />
      <ConfirmDialog
        open={unlockDialogOpen}
        confirm={handleLock}
        source="lock"
        cancel={() => setUnlockDialogOpen(false)}
        action="remove"
      />
      <ConfirmDialog
        open={removeTSCanCredentialsDialogOpen}
        confirm={handleRemoveTSCanCredentials}
        source="setTSCanCredentials"
        cancel={() => setRemoveTSCanCredentialsDialogOpen(false)}
        action="remove"
      />
      <Box p={1} display="block" textAlign="right">
        <Button
          variant="contained"
          onClick={handleSetTSCanCredentialsDialog}
          color="primary"
          style={{ margin: '2px' }}
          label="resources.engines.actions.setTSCanCredentials"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_ENGINES)}
        />
        <Button
          variant="contained"
          onClick={handleRemoveTSCanCredentialsDialog}
          color="primary"
          style={{ margin: '2px' }}
          label="resources.engines.actions.removeTSCanCredentials"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_ENGINES)}
        />
        <Button
          variant="contained"
          onClick={handleSetStandbyModeDialog}
          color="primary"
          style={{ margin: '2px' }}
          label="resources.engines.actions.setStandbyMode"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_ENGINES)}
        />
        <Button
          variant="contained"
          onClick={handleLockDialog}
          color="primary"
          style={{ margin: '2px' }}
          label={
            engine?.disabled
              ? 'resources.engines.actions.unlock'
              : 'resources.engines.actions.lock'
          }
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_ENGINES)}
        />
        <EditButton
          basePath={`/${ResourceName.ENGINES}`}
          record={engine}
          variant="contained"
          style={{ margin: '2px' }}
          disabled={!hasAuthority(Authority.EDIT_ENGINES)}
        />
      </Box>
    </>
  )
}

const EngineTabs = ({ ...props }) => {
  const translate = useTranslate()
  const { record: engine } = useShowContext<EngineDto>()
  const hasAuthority = useHasAuthority()

  const EventsFilter = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="name" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterVerifierDeviceId"
        alwaysOn
      />
    </Filter>
  )

  const EntrancesFilter = (filterProps: FilterProps) => (
    <Filter {...filterProps}>
      <TextInput source="note" alwaysOn />
      <BooleanInput
        label="common.related-records.actions.attached-only"
        source="filterVerifierDeviceId"
        alwaysOn
      />
    </Filter>
  )

  return (
    <TabbedShowLayout {...props}>
      <Tab label="resources.engines.tabs.general">
        <TextField label="ID" source="id" />
        <TextField source="name" />
        <FunctionField<EngineDto>
          source="isWatched"
          render={(record?: EngineDto) => (
            <div>
              {record?.isWatched
                ? translate('const.yes')
                : translate('const.no')}
            </div>
          )}
        />
        <DateField
          source="lastRefresh"
          showTime
          options={{
            day: '2-digit',
            month: '2-digit',
            year: 'numeric',
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit',
          }}
        />
        <TextField source="address" />
        <FunctionField<EngineDto>
          source="direction"
          render={(record?: EngineDto) => (
            <div>
              {translate(
                `resources.enums.deviceDirection.${record?.direction}`,
              )}
            </div>
          )}
        />
        {engine?.categoryId && hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
          <ReferenceField
            source="categoryId"
            reference={ResourceName.DEVICE_CATEGORIES}
            link="show"
          >
            <TextField source="name" />
          </ReferenceField>
        )}
        {engine?.disabled && <TextField source="disabled" />}
        {engine?.stanbyMode && (
          <FunctionField<EngineDto>
            source="stanbyMode"
            render={(record?: EngineDto) => (
              <div>
                {translate(`resources.enums.standbyMode.${record?.stanbyMode}`)}
              </div>
            )}
          />
        )}
        <FunctionField<EngineDto>
          source="onlineInfixCeck"
          render={(record?: EngineDto) => (
            <div>
              {record?.onlineInfixCeck
                ? translate('const.yes')
                : translate('const.no')}
            </div>
          )}
        />
        <FunctionField<EngineDto>
          source="multiplicity"
          render={(record?: EngineDto) => (
            <div>
              {translate(
                `resources.enums.multiplicities.${record?.multiplicity}`,
              )}
            </div>
          )}
        />
        <FunctionField<EngineDto>
          source="hardwareVersion"
          render={(record?: EngineDto) => (
            <div>
              {translate(
                `resources.enums.hardwareVersions.${record?.hardwareVersion}`,
              )}
            </div>
          )}
        />
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <Tab path="events" label={translate('resources.engines.tabs.events')}>
          <>
            <RelationTab<EngineDto>
              resource={ResourceName.EVENTS}
              source="eventsIds"
              mode={hasAuthority(Authority.EDIT_EVENTS) ? 'edit' : 'show'}
              attachMethod="attachEvents"
              detachMethod="detachEvents"
              attachRequestPayload={(r, ids) => ({
                engineId: r.id,
                eventsIds: ids,
              })}
              detachRequestPayload={(r, ids) => ({
                engineId: r.id,
                eventsIds: ids,
              })}
              refetchListAfterChange={(filters) =>
                filters?.filterVerifierDeviceId === true
              }
              filters={<EventsFilter />}
              filterDefaultValues={{
                verifierDeviceId: engine?.id,
                filterVerifierDeviceId: true,
              }}
              sort={{
                field: 'id',
                order: 'DESC',
              }}
            >
              <TextField label="ID" source="id" />
              <TextField source="name" />
            </RelationTab>
          </>
        </Tab>
      )}
      {hasAuthority(Authority.VIEW_ENTRANCES) && (
        <Tab
          path="entrances"
          label={translate('resources.engines.tabs.entrances')}
        >
          <>
            <RelationTab<EngineDto>
              resource={ResourceName.ENTRANCES}
              source="entrancesIds"
              mode={hasAuthority(Authority.EDIT_ENTRANCES) ? 'edit' : 'show'}
              attachMethod="attachEntrances"
              detachMethod="detachEntrances"
              attachRequestPayload={(r, ids) => ({
                engineId: r.id,
                entrancesIds: ids,
              })}
              detachRequestPayload={(r, ids) => ({
                engineId: r.id,
                entrancesIds: ids,
              })}
              refetchListAfterChange={(filters) =>
                filters?.filterVerifierDeviceId === true
              }
              filters={<EntrancesFilter />}
              filterDefaultValues={{
                verifierDeviceId: engine?.id,
                filterVerifierDeviceId: true,
              }}
              sort={{
                field: 'id',
                order: 'DESC',
              }}
            >
              <TextField label="ID" source="id" />
              <TextField source="note" />
            </RelationTab>
          </>
        </Tab>
      )}
    </TabbedShowLayout>
  )
}

export const EngineShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <EngineTabs />
  </Show>
)
