import { makeStyles, Theme } from '@material-ui/core'
import {
  AccountBalance,
  Assessment,
  DirectionsWalk,
  CenterFocusStrong,
  Category,
  Event,
  ExitToApp,
  List,
  Person,
  PhoneAndroid,
  Receipt,
  SettingsApplications,
  Sort,
  VideoLabel,
  ViewComfy,
  AccountBalanceWallet,
  ArrowForward,
} from '@material-ui/icons'
import classNames from 'classnames'
import { getResources, ResourceDefinition } from 'ra-core'
import React, { useState } from 'react'
import {
  DashboardMenuItem,
  MenuItemLink,
  ReduxState,
  useTranslate,
  RaThemeOverrides,
} from 'react-admin'
import { useSelector } from 'react-redux'
import { ResourceName } from '../../core/ResourceName'
import { SubMenu } from './AppSubmenu'

interface AdminTheme extends Theme {
  sidebar?: {
    width?: number
    closedWidth?: number
  }
  overrides?: RaThemeOverrides
}

const useStyles = makeStyles((theme: AdminTheme) => ({
  root: {
    marginTop: theme.spacing(1) || 0,
    marginBottom: theme.spacing(1),
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  open: {
    width: theme.sidebar?.width,
  },
  closed: {
    width: theme.sidebar?.closedWidth,
  },
}))

export const AppMenu = () => {
  const open = useSelector((state: ReduxState) => state.admin.ui.sidebarOpen)
  const classes = useStyles()
  const [menuState, setMenuState] = useState({
    menuStats: true,
    menuDevices: true,
    menuConfiguration: true,
  })

  const availableResources = new Set(
    useSelector<ReduxState, ResourceDefinition[]>(getResources).map(
      (r) => r.name as ResourceName,
    ),
  )
  const translate = useTranslate()

  const handleToggle = (
    menu: 'menuStats' | 'menuDevices' | 'menuConfiguration',
  ) => setMenuState((state) => ({ ...state, [menu]: !state[menu] }))

  const anyStatsResourcesAvailable = (avRes: Set<ResourceName>) =>
    avRes.has(ResourceName.STATS_OVERALL_VIEW_LIST)

  const anyDeviceResourcesAvailable = (avRes: Set<ResourceName>) =>
    avRes.has(ResourceName.PALMS) ||
    avRes.has(ResourceName.ENGINES) ||
    avRes.has(ResourceName.DETECTORS) ||
    avRes.has(ResourceName.DEVICE_CATEGORIES) ||
    avRes.has(ResourceName.PASSAGES)
  const anyConfigurationResourcesAvailable = (avRes: Set<ResourceName>) =>
    avRes.has(ResourceName.ACCESSES) ||
    avRes.has(ResourceName.VARIANTS) ||
    avRes.has(ResourceName.ENTRANCES)

  return (
    <div
      style={{ userSelect: 'none' }}
      className={classNames(classes.root, {
        [classes.open]: open,
        [classes.closed]: !open,
      })}
    >
      <DashboardMenuItem />
      {availableResources.has(ResourceName.TICKETS) && (
        <MenuItemLink
          to={`/${ResourceName.TICKETS}`}
          primaryText={translate('resources.tickets.name')}
          leftIcon={<Receipt />}
        />
      )}
      {availableResources.has(ResourceName.CUSTOMERS) && (
        <MenuItemLink
          to={`/${ResourceName.CUSTOMERS}`}
          primaryText={translate('resources.customers.name')}
          leftIcon={<Person />}
        />
      )}
      {availableResources.has(ResourceName.LOGS) && (
        <MenuItemLink
          to={`/${ResourceName.LOGS}`}
          primaryText={translate('resources.logs.name')}
          leftIcon={<List />}
        />
      )}
      {availableResources.has(ResourceName.EVENTS) && (
        <MenuItemLink
          to={`/${ResourceName.EVENTS}`}
          primaryText={translate('resources.events.name')}
          leftIcon={<Event />}
        />
      )}
      {anyStatsResourcesAvailable(availableResources) && (
        <SubMenu
          handleToggle={() => handleToggle('menuStats')}
          isOpen={menuState.menuStats}
          name={translate('side-menu.stats.name')}
          icon={<ViewComfy />}
        >
          {availableResources.has(ResourceName.STATS_OVERALL_VIEW_LIST) && (
            <MenuItemLink
              to={`/${ResourceName.STATS_OVERALL_VIEW_LIST}`}
              primaryText={translate('side-menu.stats.overall-view')}
              leftIcon={<Sort style={{ transform: 'scaleX(-1)' }} />}
            />
          )}
        </SubMenu>
      )}
      {anyDeviceResourcesAvailable(availableResources) && (
        <SubMenu
          handleToggle={() => handleToggle('menuDevices')}
          isOpen={menuState.menuDevices}
          name={translate('side-menu.devices.name')}
          icon={<VideoLabel />}
        >
          {availableResources.has(ResourceName.PALMS) && (
            <MenuItemLink
              to={`/${ResourceName.PALMS}`}
              primaryText={translate('resources.palms.name')}
              leftIcon={<PhoneAndroid />}
            />
          )}
          {availableResources.has(ResourceName.ENGINES) && (
            <MenuItemLink
              to={`/${ResourceName.ENGINES}`}
              primaryText={translate('resources.engines.name')}
              leftIcon={<AccountBalanceWallet />}
            />
          )}
          {availableResources.has(ResourceName.DETECTORS) && (
            <MenuItemLink
              to="/detectors"
              primaryText={translate('resources.detectors.name')}
              leftIcon={<CenterFocusStrong />}
            />
          )}
          {availableResources.has(ResourceName.PASSAGES) && (
            <MenuItemLink
              to={`/${ResourceName.PASSAGES}`}
              primaryText={translate('resources.passages.name')}
              leftIcon={<ArrowForward />}
            />
          )}
          {availableResources.has(ResourceName.DEVICE_CATEGORIES) && (
            <MenuItemLink
              to="/device-categories"
              primaryText={translate('resources.device-categories.name')}
              leftIcon={<Category />}
            />
          )}
        </SubMenu>
      )}
      {anyConfigurationResourcesAvailable(availableResources) && (
        <SubMenu
          handleToggle={() => handleToggle('menuConfiguration')}
          isOpen={menuState.menuConfiguration}
          name={translate('side-menu.configuration.name')}
          icon={<SettingsApplications />}
        >
          {availableResources.has(ResourceName.ACCESSES) && (
            <MenuItemLink
              to={`/${ResourceName.ACCESSES}`}
              primaryText={translate('resources.accesses.name')}
              leftIcon={<ExitToApp />}
            />
          )}
          {availableResources.has(ResourceName.VARIANTS) && (
            <MenuItemLink
              to={`/${ResourceName.VARIANTS}`}
              primaryText={translate('resources.variants.name')}
              leftIcon={<Assessment />}
            />
          )}
          {availableResources.has(ResourceName.ENTRANCES) && (
            <MenuItemLink
              to={`/${ResourceName.ENTRANCES}`}
              primaryText={translate('resources.entrances.name')}
              leftIcon={<DirectionsWalk />}
            />
          )}
        </SubMenu>
      )}
      {availableResources.has(ResourceName.OBJECTS) && (
        <MenuItemLink
          to={`/${ResourceName.OBJECTS}`}
          primaryText={translate('resources.objects.name')}
          leftIcon={<AccountBalance />}
        />
      )}
    </div>
  )
}
