const mapping: Record<
  string,
  (
    lambdaParameter: string,
    value: string | number | boolean,
    otherValues: any,
  ) => string | null
> = {
  name: (lambdaParameter, value) =>
    `${lambdaParameter}.Name.Contains("${value}")`,
  filterEventId: (lambdaParameter, value, otherValues) =>
    value
      ? `${lambdaParameter}.Events.Any(e=>e.Id==${otherValues?.eventId})`
      : null,
  eventId: () => null,
}

const filterMapper = (key: string) =>
  mapping[key] as (
    lambdaParameter: string,
    value: string | number | boolean,
    otherValues: any,
  ) => string

export default filterMapper
