import { Box, Divider } from '@material-ui/core'
import React from 'react'
import {
  DateField,
  EditButton,
  FunctionField,
  ReferenceField,
  Show,
  ShowProps,
  SimpleShowLayout,
  TextField,
  TitleProps,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { DetectorDto } from '../../../core/dto/device/detector/detector.dto'
import { ResourceName } from '../../../core/ResourceName'
import { useHasAuthority } from '../../hooks/useHasAuthority'

interface EngineTitleProps extends TitleProps {
  record?: DetectorDto
}

const ShowTitle = (data: EngineTitleProps) => {
  const { record: detector } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.detectors.titles.show')}: {detector?.id}.{' '}
      {detector?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: detector } = useShowContext<DetectorDto>()
  const hasAuthority = useHasAuthority()
  return (
    <>
      <Divider />
      <Box p={1} display="block" textAlign="right">
        <EditButton
          basePath={`/${ResourceName.DETECTORS}`}
          record={detector}
          variant="contained"
          disabled={!hasAuthority(Authority.EDIT_DETECTORS)}
        />
      </Box>
    </>
  )
}

const Detector = ({ ...props }) => {
  const translate = useTranslate()
  const { record: engine } = useShowContext<DetectorDto>()
  const hasAuthority = useHasAuthority()
  return (
    <SimpleShowLayout {...props}>
      <TextField source="id" label="ID" />
      <TextField source="name" />
      <FunctionField<DetectorDto>
        source="isWatched"
        render={(record?: DetectorDto) => (
          <div>
            {record?.isWatched ? translate('const.yes') : translate('const.no')}
          </div>
        )}
      />
      <DateField
        source="lastRefresh"
        showTime
        options={{
          day: '2-digit',
          month: '2-digit',
          year: 'numeric',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
        }}
      />
      {engine?.categoryId && hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceField
          source="categoryId"
          reference={ResourceName.DEVICE_CATEGORIES}
          link="show"
        >
          <TextField source="name" />
        </ReferenceField>
      )}
      <TextField source="address" />
      <FunctionField<DetectorDto>
        source="hardwareVersion"
        render={(record?: DetectorDto) => (
          <div>
            {translate(
              `resources.enums.hardwareVersions.${record?.hardwareVersion}`,
            )}
          </div>
        )}
      />
      <GeneralActions />
    </SimpleShowLayout>
  )
}

export const DetectorShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <Detector />
  </Show>
)
