const mapping: Record<
  string,
  (lambdaParameter: string, value: any, otherValues: any) => string | null
> = {
  note: (lambdaParameter, value) =>
    `${lambdaParameter}.Note.Contains("${value}")`,
  filterVerifierDeviceId: (lambdaParameter, value, otherValues) =>
    value
      ? `${lambdaParameter}.VerifierDevices.Any(vd=>vd.Id==${otherValues?.verifierDeviceId})`
      : null,
  verifierDeviceId: () => null,
}

const filterMapper = (key: string) =>
  mapping[key] as (
    lambdaParameter: string,
    value: any,
    otherValues: any,
  ) => string

export default filterMapper
