import { Box, Divider } from '@material-ui/core'
import React from 'react'
import {
  EditButton,
  ReferenceField,
  Show,
  ShowProps,
  SimpleShowLayout,
  TextField,
  TitleProps,
  useShowContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../core/auth/Authority'
import { DeviceCategoryDto } from '../../core/dto/device-category/device-category.dto'
import { ResourceName } from '../../core/ResourceName'
import { useHasAuthority } from '../hooks/useHasAuthority'

interface DeviceCategoryTitleProps extends TitleProps {
  record?: DeviceCategoryDto
}

const ShowTitle = (data: DeviceCategoryTitleProps) => {
  const { record: deviceCategory } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.device-categories.titles.show')}:{' '}
      {deviceCategory?.id}. {deviceCategory?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: deviceCategory } = useShowContext<DeviceCategoryDto>()

  return (
    <>
      <Divider />
      <Box p={1} display="block" textAlign="right">
        <EditButton
          basePath={`/${ResourceName.DEVICE_CATEGORIES}`}
          record={deviceCategory}
          variant="contained"
          style={{ margin: '2px' }}
        />
      </Box>
    </>
  )
}

const DeviceCategroy = ({ ...props }) => {
  const { record: deviceCategory } = useShowContext<DeviceCategoryDto>()
  const hasAuthority = useHasAuthority()

  return (
    <SimpleShowLayout {...props}>
      <TextField label="ID" source="id" />
      <TextField source="name" />
      {deviceCategory?.parentId &&
        hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
          <ReferenceField
            source="parentId"
            reference={ResourceName.DEVICE_CATEGORIES}
            link="show"
          >
            <TextField source="name" />
          </ReferenceField>
        )}
      <GeneralActions />
    </SimpleShowLayout>
  )
}

export const DeviceCategoryShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <DeviceCategroy />
  </Show>
)
