const MultiModuleStatus = (props: MultiModuleStatusProps) => {
  const classes = useStyles()
  const { moduleInfo } = props

  return (
    <>
      {moduleInfo.map((record) => (
        <Accordion key={record.id}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            className={
              new Date(record.lastRefresh).valueOf() < Date.now() - 90000
                ? classes.terminatedModuleHeader
                : ''
            }
          >
            <RecordContextProvider value={record}>
              <TextField variant="h6" source="module.alias" />
            </RecordContextProvider>
          </AccordionSummary>
          <AccordionDetails
            className={
              new Date(record.lastRefresh).valueOf() < Date.now() - 90000
                ? classes.terminatedModuleDetails
                : ''
            }
          >
            <ModuleStatus moduleInfo={record} disableAliasHeader />
          </AccordionDetails>
        </Accordion>
      ))}
    </>
  )
}

const DeviceStatusesTab = (props: StatusTabProps) => {
  const { jsonStatus: rawJsonStatus } = props
  const jsonStatus = JSON.parse(rawJsonStatus) as Array<any>
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const modules = Object.entries(jsonStatus).map(([key, value]) =>
    moduleMapper(camelcaseKeys(value, { deep: true }), key),
  )

  return (
    <MultiModuleStatus
      moduleInfo={modules
        .filter((m) => m != null)
        .map((m) => m as ModuleBaseDto)}
    />
  )
}

interface ModuleBaseStatusProps {
  readonly moduleInfo: ModuleBaseDto
  readonly disableAliasHeader?: boolean
  readonly children?: ReactNode[]
}

interface ModuleStatusProps {
  readonly moduleInfo: ModuleBaseDto
  readonly disableAliasHeader?: boolean
}

interface MultiModuleStatusProps {
  readonly moduleInfo: ModuleBaseDto[]
}

interface StatusTabProps {
  readonly jsonStatus: any
}

export { ModuleStatus, MultiModuleStatus, DeviceStatusesTab }
