import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Datagrid,
  Filter,
  List,
  ListProps,
  Pagination,
  ReferenceField,
  ReferenceInput,
  TextField,
  TextInput,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { ResourceName } from '../../../core/ResourceName'
import FilterProps from '../../common/FilterProps'
import QuickFilter from '../../common/QuickFilter'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const EngineFilters = (props: FilterProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <Filter {...props}>
      <TextInput source="name" alwaysOn />
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceInput
          label="resources.palms.fields.categoryId"
          source="categoryIdWithDescendants"
          reference={ResourceName.DEVICE_CATEGORIES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      <QuickFilter source="eventActive" defaultValue />
    </Filter>
  )
}

const EngineGrid = ({ ...props }) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()
  return (
    <Datagrid
      {...props}
      size="small"
      rowClick={(id) => `${ResourceName.ENGINES}/${id}/show`}
      hasBulkActions={false}
    >
      <TextField source="id" label="ID" />
      <TextField source="name" />
      {!smallScreen && <TextField source="address" />}
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceField
          source="categoryId"
          reference={ResourceName.DEVICE_CATEGORIES}
          link="show"
        >
          <TextField source="name" />
        </ReferenceField>
      )}
      {!smallScreen && <TextField source="disabled" sortable={false} />}
    </Datagrid>
  )
}

export const EngineList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    filters={<EngineFilters />}
  >
    <EngineGrid />
  </List>
)
