import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Create,
  CreateProps,
  ReferenceInput,
  required,
  SimpleForm,
  TextInput,
  useTranslate,
} from 'react-admin'
import { useLocation } from 'react-router'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'

const SeatCreate = (props: CreateProps) => {
  const translate = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const { search } = useLocation()
  const objectIdRaw = new URLSearchParams(search).get('objectId')
  const objectId: number | undefined =
    objectIdRaw !== null ? Number(objectIdRaw) : undefined

  return (
    <Create {...props} title={translate('resources.seats.create')}>
      <SimpleForm redirect={`/objects/${objectId}/show/3`}>
        <ReferenceInput
          validate={required()}
          source="blockId"
          reference={ResourceName.BLOCKS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
          filter={{ objectId }}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
        <TextInput source="numberAlias" />
        <TextInput source="rowAlias" />
      </SimpleForm>
    </Create>
  )
}

export default SeatCreate
