import { EventStatus } from '../../enum/EventStatus'

const mapping: Record<
  string,
  (lambdaParameter: string, value: any, otherValues: any) => string | null
> = {
  name: (lambdaParameter, value) =>
    `${lambdaParameter}.Name.Contains("${value}")`,
  filterEventId: (lambdaParameter, value, otherValues) =>
    value
      ? `${lambdaParameter}.Events.Any(e=>e.Id==${otherValues?.eventId})`
      : null,
  eventId: () => null,
  eventActive: (lambdaParameter) =>
    `${lambdaParameter}.Events.Any(ev=>ev.Status.Equals(TicketAPI.Models.Enums.TicketManagement.EventStatuses.${EventStatus.ACTIVE}))`,
  categoryIdWithDescendants: () => null,
  categoriesIds: (lambdaParameter, value) =>
    `new int%3F[] {${(
      value as number[]
    )?.toString()}}.Contains(${lambdaParameter}.CategoryId)`,
  filterEntranceId: (lambdaParameter, value, otherValues) =>
    value
      ? `${lambdaParameter}.Entrances.Any(e=>e.Id==${otherValues?.entranceId})`
      : null,
  entranceId: () => null,
}

const filterMapper = (key: string) =>
  mapping[key] as (
    lambdaParameter: string,
    value: string | number | boolean,
    otherValues: any,
  ) => string

export default filterMapper
