import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Create,
  CreateProps,
  ReferenceInput,
  required,
  SelectInput,
  SimpleForm,
  TextInput,
  useTranslate,
} from 'react-admin'
import { useLocation } from 'react-router'
import { BlockType } from '../../../core/enum/BlockType'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'

const BlockCreate = (props: CreateProps) => {
  const translate = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const { search } = useLocation()
  const objectIdRaw = new URLSearchParams(search).get('objectId')
  const objectId: number | undefined =
    objectIdRaw !== null ? Number(objectIdRaw) : undefined

  return (
    <Create {...props} title={translate('resources.blocks.create')}>
      <SimpleForm redirect={`/objects/${objectId}/show/2`}>
        <ReferenceInput
          validate={required()}
          source="tribuneId"
          reference={ResourceName.TRIBUNES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
          filter={{ objectId }}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
        <TextInput source="name" validate={required()} />
        <TextInput source="code" />
        <SelectInput
          source="type"
          choices={Object.entries(BlockType).map(([value]) => ({
            id: value,
            name: `resources.enums.blockType.${value}`,
          }))}
        />
      </SimpleForm>
    </Create>
  )
}

export default BlockCreate
