import { ConfirmDialog, SetTSCanCredentialsModal } from './PassageDialogs'
import { SPOM4FullAddressField } from './SPOM4Address'

const useStyles = makeStyles(() =>
  createStyles({
    generalActions: {
      margin: '2px',
    },
  }),
)

interface PassageTitleProps extends TitleProps {
  record?: PassageDto
}

const ShowTitle = (data: PassageTitleProps) => {
  const { record: passage } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.passages.titles.show')}: {passage?.id}.{' '}
      {passage?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: passage, basePath } = useShowContext<PassageDto>()
  const [tsCanCredentialsModalOpen, setTSCanCredentialsModalOpen] =
    useState(false)
  const [
    removeTSCanCredentialsDialogOpen,
    setRemoveTSCanCredentialsDialogOpen,
  ] = useState(false)
  const hasAuthority = useHasAuthority()
  const classes = useStyles()
  const [mutate] = useMutation()
  const refresh = useRefresh()

  const handleSetTSCanCredentialsDialog = () => {
    setTSCanCredentialsModalOpen(true)
  }

  const handleRemoveTSCanCredentials = async () => {
    setRemoveTSCanCredentialsDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('removeTSCanCredentials'),
        resource: ResourceName.PASSAGES,
        payload: {
          passageId: passage?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleRemoveTSCanCredentialsDialog = () => {
    setRemoveTSCanCredentialsDialogOpen(true)
  }

  return (
    <>
      <Divider />
      <SetTSCanCredentialsModal
        open={tsCanCredentialsModalOpen}
        close={() => setTSCanCredentialsModalOpen(false)}
      />
      <ConfirmDialog
        open={removeTSCanCredentialsDialogOpen}
        confirm={handleRemoveTSCanCredentials}
        source="setTSCanCredentials"
        cancel={() => setRemoveTSCanCredentialsDialogOpen(false)}
        action="remove"
      />
      <Box p={1} display="block" textAlign="right">
        <Button
          className={classes.generalActions}
          variant="contained"
          onClick={handleSetTSCanCredentialsDialog}
          color="primary"
          label="resources.passages.actions.setTSCanCredentials"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_PASSAGES)}
        />
        <Button
          className={classes.generalActions}
          variant="contained"
          onClick={handleRemoveTSCanCredentialsDialog}
          color="primary"
          label="resources.passages.actions.removeTSCanCredentials"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_PASSAGES)}
        />
        <EditButton
          className={classes.generalActions}
          basePath={basePath}
          record={passage}
          variant="contained"
          disabled={!hasAuthority(Authority.EDIT_PASSAGES)}
        />
      </Box>
    </>
  )
}

const CountersActions = () => {
  const { record: passage } = useShowContext<PassageDto>()
  const [countersModalOpen, setCountersModalOpen] = useState(false)
  const hasAuthority = useHasAuthority()
  const classes = useStyles()
  const [mutate] = useMutation()
  const refresh = useRefresh()

  const countersEmpty = () =>
    (!passage?.freeEntrancesCount || passage.freeEntrancesCount === 0) &&
    (!passage?.freeExitsCount || passage.freeExitsCount === 0) &&
    (!passage?.ticketEntrancesCount || passage.ticketEntrancesCount === 0) &&
    (!passage?.ticketExitsCount || passage.ticketExitsCount === 0) &&
    (!passage?.unauthorizedEntrancesCount ||
      passage.unauthorizedEntrancesCount === 0) &&
    (!passage?.unauthorizedExitsCount || passage.unauthorizedExitsCount === 0)

  const handleResetCounters = async () => {
    setCountersModalOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('resetCounters'),
        resource: ResourceName.PASSAGES,
        payload: {
          passageId: passage?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleResetCountersDialog = () => {
    setCountersModalOpen(true)
  }

  return (
    <>
      <Divider />
      <ConfirmDialog
        open={countersModalOpen}
        confirm={handleResetCounters}
        source="setTSCanCredentials"
        cancel={() => setCountersModalOpen(false)}
        action="remove"
      />
      <Box p={1} display="block" textAlign="right">
        <Button
          className={classes.generalActions}
          variant="contained"
          onClick={handleResetCountersDialog}
          color="primary"
          label="resources.passages.actions.resetCounters"
          useSmallVersionBreakpoint={false}
          disabled={!hasAuthority(Authority.EDIT_PASSAGES) || countersEmpty()}
        />
      </Box>
    </>
  )
}

const CountersTab = ({ ...props }) => (
  <SimpleShowLayout {...props}>
    <TextField source="freeEntrancesCount" />
    <TextField source="freeExitsCount" />
    <TextField source="ticketEntrancesCount" />
    <TextField source="ticketExitsCount" />
    <TextField source="unauthorizedEntrancesCount" />
    <TextField source="unauthorizedExitsCount" />
    <CountersActions />
  </SimpleShowLayout>
)

